import React from 'react'
import '../../styles/pages/Home.css';
import { Zoom } from "react-reveal";

function Home() {
    return (
        <Zoom>
        <div className='home-description-container'>
            <p><span className='badge text-bg-dark'>SickGamers</span> es una pgina web especializada en la venta de indumentaria y accesorios de gaming para gamers y amantes de los videojuegos. La pgina cuenta con una gran variedad de productos, desde camisetas, sudaderas y chaquetas, hasta gorras, calcetines y accesorios para el cabello, todos ellos con diseos temticos y exclusivos para el pblico gamer.
                <hr></hr>
                Adems de la venta de productos, <span className='badge text-bg-dark'>SickGamers</span> tambin ofrece informacin relevante para los gamers, como noticias, reseas de juegos, entrevistas a personalidades de la industria y guas de compra de hardware y software.
                <hr></hr>
                La pgina cuenta con una interfaz intuitiva y fcil de usar, con categoras claras y precisas para navegar por los productos y encontrar lo que se busca con rapidez. Los usuarios tambin pueden crear una cuenta en la pgina para recibir ofertas exclusivas y acceso a contenido adicional.
                <hr></hr>
                <span className='badge text-bg-dark'>SickGamers</span> se enfoca en ofrecer productos de alta calidad y durabilidad a precios competitivos, y se esfuerza por brindar una experiencia de compra satisfactoria a todos sus clientes. En resumen, es una pgina web ideal para los gamers que buscan indumentaria y accesorios de calidad con diseos exclusivos y temticos para demostrar su pasin por los videojuegos.</p>
            </div>
            </Zoom>
    )
}

export default Home